import React from 'react';
import './ModalWindow.css';
export class ModalButton extends React.Component {
    constructor(props) {
      super(props)
      this.state = { isModalOpen: false }
      this.state.GeoJSON = this.props.GeoJSON;
      this.state.access = "public";
      this.state.owner = "admin";
      this.handleClick = this.handleClick.bind(this);
    };
    handleClick(e)
    {
      var JSONtoBack = {
        owner: this.state.owner,
        access: this.state.access,
        GeoJSON:this.state.GeoJSON,
      }
      fetch('http://diploma/layers.php',{
        method: 'POST',
        body:JSON.stringify("example"),
        headers: {
        // 'Authorization': 'bearer ${token}',
        'Content-Type': 'application/json'
          }
        }).then(response => console.log(response)).then(data => console.log(data));
      console.log("и тут мы отправим данные на сервак");
      e.preventDefault();
    }
    componentDidUpdate()
    {
      if (this.state.GeoJSON != this.props.GeoJSON)
      {
        this.setState({GeoJSON:this.props.GeoJSON})
      }
    }
    render() {
      return (
        <div>
          <button onClick={() => this.openModal()}>Open modal</button>
          <Modal isOpen={this.state.isModalOpen} onClose={() => this.closeModal()}>
            <form onSubmit={this.handleClick}>
            <label>
              Layer`s name:
              <input type="" value="" onChange="" />
              <input type="submit" value="Сохранить" onClick=""/>  
            </label>
           </form>
          </Modal>
        </div>
      )
    }

    openModal() {
      this.setState({ isModalOpen: true })
    }

    closeModal() {
      this.setState({ isModalOpen: false })
    }
  }

export class Modal extends React.Component {
    render() {
      if (this.props.isOpen === false)
        return null

      return (
        <div>
          <div className="modal">
            {this.props.children}
          </div>
          <div className="bg" onClick={e => this.close(e)}/>
        </div>
      )
    }

    close(e) {
      e.preventDefault()

      if (this.props.onClose) {
        this.props.onClose()
      }
    }
  }